.Layout_layout__2ixcT {
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background-color: #191e2c;
}

.FormCard_formCard__3C1-5 {
    width: 500px;
    min-width: auto;
}


.GalleryItem_galleryItem__1a3Wv {
    box-sizing: border-box;
    background-color: #2b2f3e;
    width: 100%;
    border-radius: 5px;
    padding: 5px 10px;
    position: relative;
}

.GalleryItem_thumbnail_container__37xCQ {
    box-sizing: border-box;
    border-radius: 5px;

    width: 100%;
    height: 200px;
    max-height: 200px;
    overflow: hidden;
}

.GalleryItem_thumbnail__2uZjL {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    object-fit: cover;
}

.Options_options__3yhaW {
    display: flex;
    flex-direction: column;

    background-color: #191e2c;
    color: #fff;
    border-radius: 5px;

    overflow: hidden;

    opacity: 0;
    display: hidden;
    z-index: -100;

    position: absolute;
    top: 20px;
    right: 10px;

    transition: opacity 0.3s ease-in;
}

.Options_options__3yhaW.Options_show__fSJpq {
    opacity: 1;
    display: flex;
    z-index: 100;
}

.Options_options__3yhaW .Options_option__133mk {
    text-decoration: none;
    color: #fff;
    padding: 10px 15px;
    cursor: pointer;

    transition: all 0.3s ease-in-out;
}

.Options_options__3yhaW .Options_option__133mk:hover {
    background-color: #000;
    color: #fff;
}

